$font-family: "Montserrat", sans-serif;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: rgb(44, 44, 44);
  color: white;
  font-family: $font-family;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5rem;

  &__title {
    margin-right: 1rem;
  }

  &__img {
    width: 2rem;
  }
}

.form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  &__input {
    height: 2rem;
    border: 1px solid white;
    border-radius: 8px;
    margin-right: 1rem;
    padding-left: 0.5rem;
    background-color: rgb(248, 248, 248);
    color: black;
    outline: none;
  }

  &__button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    background-color: white;
    color: black;
    border: none;
    cursor: pointer;
    font-size: 2rem;
  }
}
